.layout{width:100vw;height:100vh;position:relative;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;overflow:hidden}

.textCollapsed{width:100%;font-size:.75rem;color:#c8c8c8;padding:1rem 0 0 0;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;cursor:pointer;transition:all .2s}.textCollapsed.active{white-space:normal}

.cart{position:absolute;left:0;bottom:0;z-index:103;width:100%;border-radius:25px 25px 0 0;transition:all .4s}.cart-blur{width:100vw;height:100vh;position:fixed;left:0;bottom:0;z-index:101;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);transition:all .4s}.cart-blur.deactive{z-index:-101;width:0;height:0}.cart-header{position:relative;display:flex;justify-content:center;align-items:center;height:2.5rem;width:100%;border-radius:25px 25px 0 0;font-weight:500;color:white;z-index:104;background-color:#2F2E30;cursor:pointer}.cart-header svg{fill:white;width:1rem;height:1rem}.cart-header::before{content:"";height:1px;width:5%;position:absolute;background-color:white;top:.325rem;left:50%;transform:translateX(-50%)}.cart-body{width:100%;transition:all .6s;border-radius:0;height:6.5rem;padding:.25rem 1.5rem;z-index:104;background-color:#2F2E30}.cart-body.big{height:75vh}.cart-body.deactive{height:0}@-webkit-keyframes testAnim{0%{transform:translate(0, 30px)}100%{transform:translate(0, 0px)}}@keyframes testAnim{0%{transform:translate(0, 30px)}100%{transform:translate(0, 0px)}}

